<template>
  <div class="todo-footer" v-show="todoList.length>0">
    <label>
      <input type="checkbox" v-model="isSelectAll" @change="selectAll"/>
    </label>
        <span>
          <span>已完成{{oks}}</span> / 全部{{todoList.length}}
        </span>
    <button class="btn btn-danger" @click="delAllSelect">清除已完成任务</button>
  </div>
</template>

<script>
export default {
  name: "TodoFooter",
  props:["todoList"],
  computed:{
    oks(){
      return this.todoList.reduce((t,c) => t+=c.ok?1:0,0)
    },
    isSelectAll(){
      return this.oks === this.todoList.length
    }
  },methods:{
    selectAll(e){
      let checked=e.target.checked
      if (checked){
        for (let i of this.todoList){
          i.ok=true
        }
      }else {
        for (let i of this.todoList){
          i.ok=false
        }
      }
    },
    delAllSelect(){
      this.$emit("delAllSelect")
    }
  }
}
</script>

<style scoped>
/*footer*/
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}

.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.todo-footer button {
  float: right;
  margin-top: 5px;
}
</style>