<template>
  <div id="app">
    <div class="todo-container">
      <div class="todo-wrap">
        <!-- 顶部搜索框组件 -->
        <SearchBar @add="addTodo"/>
        <!-- 任务列表展示组件 -->
        <TodoList @del="delTodo" :todo-list="todoList"/>
        <!-- 页脚组件 -->
        <TodoFooter :todo-list="todoList" @delAllSelect="delAllSelect"/>
      </div>
    </div>
  </div>
</template>

<script>
import SearchBar from "@/components/SearchBar";
import TodoList from "@/components/TodoList";
import TodoFooter from "@/components/TodoFooter";
import {v4 as uuidv4} from "uuid"
export default {
  name: 'App',
  components: {
    TodoFooter,
    SearchBar,TodoList
  },
  data(){
    return{
      todoList:[
        {id:"001",name:"吃饭",ok:true},
        {id:"002",name:"睡觉",ok:false},
      ]
    }
  },
  methods:{
    addTodo(value){
      let todo={id:uuidv4(),name:value,ok:false}
      this.todoList.unshift(todo)
    },
    delTodo(id) {
       this.todoList=this.todoList.filter(item => item.id!==id)
    },
    delAllSelect(){
      this.todoList=this.todoList.filter(item => !item.ok)
    }
  }
}
</script>

