<template>
  <div>
    <Todo @del="delTodo" v-for="item in todoList" :key="item.id" :todo="item"/>
  </div>
</template>

<script>
import Todo from "@/components/Todo";
export default {
  name: "TodoList",
  components: {Todo},
  props:["todoList"],
  methods:{
    delTodo(id){
      this.$emit("del",id)
    }
  }
}
</script>

<style scoped>
/*main*/
.todo-main {
  margin-left: 0px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0px;
}

.todo-empty {
  height: 40px;
  line-height: 40px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding-left: 5px;
  margin-top: 10px;
}
</style>